import '../App.css';
import {useState, useEffect, useRef} from 'react';
import {InView} from 'react-intersection-observer';
import wallpaper from '../media/images/wallpaper.jpg'
import TypeIt from "typeit-react";
import backgroundSong from '../media/audio/background_song.mp3'
import autosize from "autosize/dist/autosize";

function Home(props) {
    // const {scrollCoverage, setScrollCoverage} = useState();
    // const {lookDuration, setLookDuration} = useState(0);
    let scrollCoverage = 0, lookDuration = 0, lastIntervalId, lastScrollTop = 0;

    const [auth, setAuth] = useState(false)
    // const {passwordInput, setPasswordInput} = useState("")

    const typeItOptions = {cursor: false, lifeLike: true, waitUntilVisible: true}

    const scrollWrapper = useRef(null)
    const inputField = useRef(null)
    const audioElement = useRef(null)

    const passwords = "1554569165"

    let infoPack = useRef({
        scrollCoverage: scrollCoverage,
        scrollHeight: 0,
        clientHeight: 0,
        stops: [],
        clicks: []
    })

    const stopWatch = () => setInterval(() => {
        lookDuration += 100
        // console.log(">>> Interval is running <<<")
        infoPack.current.scrollHeight = scrollWrapper.current.scrollHeight
        infoPack.current.clientHeight = scrollWrapper.current.clientHeight
    }, 100)

    useEffect(() => {
        console.log(">>> StopWatch initialized <<<")
        // if(auth) {
        //     lastIntervalId = stopWatch()
        // }

        console.log(props.socket)
        infoPack.current.resourceURL = window.location.pathname
        infoPack.current.scrollHeight = scrollWrapper.current.scrollHeight
        infoPack.current.clientHeight = scrollWrapper.current.clientHeight
        props.socket.emit("update_user_reactions", infoPack.current)

    }, [scrollWrapper.current])

    useEffect(()=>{
        autosize(inputField.current)
    }, [inputField.current])


    return !auth ? (
        <div className="font-sans" ref={scrollWrapper}>
            <div className="px-8 py-4 h-[100vh] align-middle font-bold">
                <label style={{direction: "rtl"}} htmlFor="password"
                       className="block mb-2 text-1xl font-medium text-gray-900 dark:text-white">الباسوورد رقم تليفوني الحقيقي (المصري):</label>

                <input onChange={(event)=>{
                    console.log(event)
                    infoPack.current = {
                        ...infoPack.current,
                        passwordProvided: event.target.value
                    }
                    props.socket.emit("update_user_reactions", infoPack.current)
                    if(event.target.value.includes(passwords)) {
                        setAuth(true)
                    }
                }} style={{direction: "rtl"}} type="password" id="password" className="mb-5 block w-full p-4 text-1xl text-gray-900 border border-gray-300 rounded-lg bg-gray-50 sm:text-md focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" />
                {/*<button onClick={()=>{*/}
                {/*    */}
                {/*}} style={{direction: "rtl"}} type="submit"*/}
                {/*        className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">دخلني يا سيدي أنا بطاطا بين رجليك</button>*/}
            </div>
        </div>
    ) : (
        <div className="font-sans" ref={scrollWrapper} onScroll={event => {

            let target = event.target;

            clearInterval(lastIntervalId)

            let scrolledTo = target.scrollTop / (target.scrollHeight - target.clientHeight)

            scrollCoverage = target.scrollTop - lastScrollTop > 0 ? target.scrollTop / (target.scrollHeight - target.clientHeight) : scrollCoverage

            console.log(`The user stopped scrolling for ${lookDuration / 1000}s`)
            console.log(`The scroll coverage is ${scrollCoverage}`)

            infoPack.current.scrollCoverage = scrollCoverage

            if (lookDuration) {
                infoPack.current.stops.push({
                    y: scrolledTo,
                    duration: lookDuration
                })
            }

            props.socket.emit('update_user_reactions', infoPack.current)

            lookDuration = 0
            lastIntervalId = stopWatch()
            lastScrollTop = target.scrollTop > lastScrollTop ? target.scrollTop : lastScrollTop
        }} onClick={(event) => {
            infoPack.current.clicks.push({
                x: event.pageX,
                y: event.pageY,
                scrollTop: scrollWrapper.current.scrollTop
            })
            props.socket.emit('update_user_reactions', infoPack.current)
            console.log(infoPack.current)
        }} style={{
            position: 'fixed',
            height: '100vh',
            width: '100vw',
            overflowY: 'scroll'
        }}>
            <div className="bg-black text-white">
                <div style={{
                    // backdropFilter: "blur(10px)",
                    height: "100%",
                }}>
                    <div className="m-auto flex text-right container items-center px-8 py-4 text-2xl leading-[3rem]">
                        <div className="w-full" style={{direction: 'rtl'}}>الكلام في بالي كتير عايز أقوله بس مبقتش فاضيلك زي الأول يا لبوة، بس عموماً خليكي متابعة، ربنا عارف وأنا عارف وانت عارفة إن أنا كنت الوحيد اللي حنين عليكي بجد ومكنش فيه حد أحن عليكي مني، دانا أحن عليكي من أمك يا شيخة، ومكنتش مستني منك حاجة إلا إنك متكونيش شرموطة وخاينة وبصباصة ومعندكيش رضا، وعمرك بغبائك - اللي كنت بحبه بس دلوقتي كرهته - ما فكرتي هو ليه العرص ده مش شايف حد غيري مع إني عِجلة وفلات وشكلي شبه التريلة القلاب وسايب كل النسوان الجامدة ديه ولا فكر حتى يحاول معاهم زي ما أنا وسخة كدة ومدلوقة ع الرجالة وبحاول معاهم، والعزة أخذتك بالإثم وفكرتي بتعريصي ليكي الكتير إن محدش معبرني غيرك أو إنك فعلاً تستحقي الكلام ده مع إن كلامي كان كله عشان بحبك، وأنا الوحيد اللي عمري ما سبتك مع إن كله سابك وهيسيبك، بس من هنا وطالع كلمة "الكل" ديه هتتمسح من قاموسك، ومش هيبقى فيه غيري، الأشكال القحبة اللي زيك لو اتسابت لدماغها هتضيع، أنا هنا بس اللي أفكر فهمتي يا كتلة غباء؟ </div>
                    </div>
                    <div className="m-auto h-[50vh] flex text-right container items-center px-8 py-4 text-2xl leading-[3rem]">
                        <div className="w-full" style={{direction: 'rtl'}}> انت محتاجة تتربي من أول وجديد، وفعلاً هتوبي ع إيدي </div>
                    </div>
                    <div className="px-8 py-4 align-middle font-bold">
                        <label style={{direction: "rtl"}} htmlFor="large-input"
                               className="block mb-2 text-1xl font-medium dark:text-white">بما إنك دخلتي فده معناه إنك ممكن تكوني محتاجة تتكلمي فقولي اللي تقوليه هنا يا عِجلة: </label>

                        <textarea onChange={()=>{
                            infoPack.current = {
                                ...infoPack.current,
                                verifierText: inputField.current.value
                            }
                            props.socket.emit("update_user_reactions", infoPack.current)
                        }} ref={inputField} style={{direction: "rtl"}} type="text" id="large-input" className="mb-2 block w-full p-4 text-1xl text-white border border-gray-300 rounded-lg bg-transparent sm:text-md focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"></textarea>
                        <button onClick={window.close} style={{direction: "rtl"}} type="submit"
                                className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">
                            وبس فعلاً هو ده اللي حصل والله يا محمد يا قلبي وأنا عبيطة كنت مفكرة نفسي bubbles وأنا شبه azmuth أصلاً
                        </button>

                    </div>
                </div>
            </div>
        </div>
    );
}

export default Home;
