import './App.css';
import Home from "./pages/Home";
import socketIO from 'socket.io-client';
import {BrowserRouter, Route, Routes} from "react-router-dom";
import Panel from "./pages/Panel"
import QRCode from "./pages/QRCode";
const socket = socketIO.connect();

function App() {
    return (
        <BrowserRouter>
            <Routes>
                <Route path="/panel" element={<Panel socket={socket} />}/>
                <Route path="/كسمك" element={<Home socket={socket}/>}/>
                <Route path="/" element={<QRCode socket={socket}/>}/>
            </Routes>
        </BrowserRouter>
    )
}

export default App;
